import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {kebabCase, toLower} from "lodash"
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';


class BlogList extends React.Component {
  render() {
    const { data, pageContext } = this.props
    const { currentPage, numPages } = pageContext
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="All posts" />
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          const tags = node.frontmatter.tags

          return (
            <div className="mt-4 mb-4 pt-4 pb-4" key={node.fields.slug}>
              <h4 className="mb-2">
                <Link style={{ boxShadow: `none`, textDecoration: `none`, color: `#1A62A3` }} to={node.fields.slug}>
                  {title}
                </Link>
              </h4>
              {/* <small>{node.frontmatter.date}</small> */}
              <div className="d-flex align-items-center mb-1">
                <div className="mr-2" style={{color: '#666666', fontSize: '13px'}}>{node.frontmatter.date}</div>
                {
                  (tags && tags.length > 0) &&
                  tags.map(tag => (
                    <Link
                        to={`/tags/${kebabCase(toLower(tag))}`}
                        className="badge badge-info pt-1 pb-1 pr-2 pl-2 ml-1"
                        style={{backgroundColor: '#B4A28F'}}
                        key={tag}
                      >
                      <FontAwesomeIcon icon={'tag'} size="xs" className={"mr-1"} />
                      {tag}
                    </Link>
                  ))
                }
              </div>
              <p
                style={{color: '#999999'}}
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </div>
          )
        })}
        <div className="d-flex justify-content-center">
          <div>
            {Array.from({ length: numPages }, (_, i) => currentPage !== i + 1 ? (
              <Link
                key={`pagination-number${i + 1}`}
                to={`/${i === 0 ? "" : i + 1}`}
                className="ml-1 mr-1"
                style={{color: '#1A62A3', fontWeight: 'bold'}}
              >
                {i + 1}
              </Link>
            ) : (<span className="ml-1 mr-1" style={{color: '#B4A28F', fontWeight: 'bold'}}>{i + 1}</span>))}
          </div>
        </div>
      </Layout>
    )
  }
}

export default BlogList

export const pageQuery = graphql`
  query blogListQuery($limit: Int!, $skip: Int!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD HH:mm:ss")
            title
            tags
          }
        }
      }
    }
  }
`